import { FC, useState } from "react";

import { Accordion, AccordionItem } from "@chakra-ui/react";

import { Expandable } from "pages/about/common/expandable/Expandable";
import { ArticleTitle } from "pages/about/common/title/Title";
import { ExperienceIcon } from "utils/Icons";

interface IExperience {
    id: string;
    company: string;
    position: string;
    duration: string;
    description: string[];
}

interface Props {
    title: string;
    experiences: IExperience[];
}

export const Experience: FC<Props> = ({title, experiences}) => {
    const [experiencesExpanded, setExperiencesExpanded] = useState<number[]>([0]);

    return (
        <>
            <ArticleTitle 
                icon={<ExperienceIcon size={20} />}
                title={title}
            />
            <br />
            <Accordion pt="2" allowMultiple index={experiencesExpanded}>
                {experiences.map((exp, idx) => (
                    <AccordionItem p="0" border="0" mb="4" key={`panel-${exp.company}`}>
                        <Expandable
                            id={exp.id}
                            title={exp.company}
                            subTitle={exp.position}
                            date={exp.duration}
                            content={exp.description}
                            idx={idx}
                            onChange={setExperiencesExpanded}
                            expanded={experiencesExpanded}
                        />
                    </AccordionItem>
                ))}
            </Accordion>
        </>
    );
};
